@font-face {
  font-family: "Onest";
  src: url("./assets/fonts/Onest-ExtraLight.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Onest";
  src: url("./assets/fonts/Onest-Light.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Onest";
  src: url("./assets/fonts/Onest-Regular.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Onest";
  src: url("./assets/fonts/Onest-Medium.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Onest";
  src: url("./assets/fonts/Onest-SemiBold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

:root {
  --main-text-color: #edc9cb;
  --selected-text-color: #e18186;
  color-scheme: dark;
}

html {
  font-family: "Onest", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #fbe0f3;
  overflow-x: hidden;
  background-image: url("/public/images/pattern.webp");
  background-repeat: repeat;
  background-size: 600px 600px;
  image-rendering: pixelated;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  font-size: 16px;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.container {
  padding: 0 3rem 0 3rem;
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
}

input {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
  outline: none;
  background: transparent;
  box-shadow: none;
}

@media (max-width: 768px) {
  .container {
    padding: 0 1rem 0 1rem;
    max-width: none;
  }
}



